        "health__description": "Reface rileva la stanchezza dei tuoi occhi e previene l'affaticamento.\nSecondo le statistiche, oltre il 70% degli impiegati ha malattie a causa della stanchezza degli occhi.\nReface ti avvertir quando i tuoi occhi si stancheranno.",
