        "policy__subtitle": "18 dicembre 2019",
        "policy__text": "Ciao!\nRaccogliamo informazioni sui nostri utenti. Qui sono elencati alcuni esempi (ma non tutti):\n Linguaggio. Per sapere quale lingua utilizzare per rimanere in contatto con te.\n E-mail. Per sapere come rimanere in contatto con te.\n Ora in cui hai inviato l'e-mail.\n I risultati del sondaggio sociale. Analizzare il nostro pubblico.\n Versione di atterraggio. Per capire il contesto.\nUtilizziamo i tuoi dati solo per sviluppare il nostro prodotto.\nPossiamo modificare questa politica sulla privacy quando vogliamo. Se usi i prodotti di Reface (inclusi sito, app ecc.), Sei d'accordo con questa politica sulla privacy.",
        "policy__title": "Politica sulla riservatezza",
        "presentation__button_preorder": "Preordinare",
        "reface__actions__description": "L'elenco delle azioni disponibili sar ampliato. Attualmente, v' l'integrazione con IFTTT via Webhook, cos come la possibilit di aggiungere le azioni da sviluppatori di terze parti tramite API (ad esempio, \"fare clic su un pulsante\").",
        "reface__actions__title": "Azioni",
        "reface__computer_connection__description": " possibile collegare un computer e gestirlo.\nIl computer deve essere sulla stessa rete WiFi del telefono.",
        "reface__computer_connection__title": "Collegamento al computer",
        "reface__diagnostics__description": " possibile visualizzare le informazioni circa l'esattezza degli elettrodi, cos come l'ultimo gesto riconosciuto.",
        "reface__diagnostics__title": "A proposito di dispositivo",
        "reface__retraining__description": "Se i vostri gesti sono erroneamente interpretate dal classificatore, si pu imparare di nuovo.\nSono disponibili due modalit di riqualificazione disponibili: manuale e automatico.\nIn modalit manuale, si registra brevi segmenti di video e di indicare dove  stato fatto il gesto. Il computer verr automaticamente fare questo per voi.",
        "reface__retraining__title": "Riqualificazione",
